/* ==========================================================================
   Header Vertical - Closed responsive style - begin
   ========================================================================== */

@include ipad-landscape {
	
	.eltdf-header-vertical-closed {
		
		.eltdf-vertical-menu-area {
			display: none;
		}

		.eltdf-vertical-area-bottom-logo {
			display: none;
		}
		
		.eltdf-content,
		footer {
			padding-left: 0;
		}
	}
}
/* ==========================================================================
   Header Vertical - Closed responsive style - end
   ========================================================================== */